<style scoped>
.header-button {
  margin-top: 8px;
  margin-left: 12px;
  margin-right: 12px;
  padding-bottom: 4px;
}
</style>
<template>
  <div style="background: #fff; padding: 0" class="relative">
    <a-button
      title="功能"
      class="absolute z-50 mt-2 mx-2 pb-4"
      type="primary"
      shape="circle"
      icon="bars"
      @click="showLeftSide"
    ></a-button>
    <span class="absolute right-0 mr-12 z-50 -mt-2">登入者</span>
    <div>
      <a-collapse :bordered="false" @change="changeCollapse">
        <a-collapse-panel key="1" :showArrow="false">
          <template slot="header">
            <span class="ml-10">Header</span>
          </template>
          <div id="patDetail">
            <a-divider orientation="left">病患基本資料</a-divider>
            <p>1111</p>
            <p>1111</p>
            <p>1111</p>
            <p>1111</p>
            <p>1111</p>
            <p>1111</p>
            <p>1111</p>
            <p>1111</p>
            <p>1111</p>
          </div>
        </a-collapse-panel>
      </a-collapse>
    </div>
    <a-affix :offsetTop="top">
      <a-button
        title="系統"
        class="absolute top-0 right-0 mt-2 mx-2 pb-4 z-50"
        shape="circle"
        type="primary"
        icon="appstore"
        @click="ShowRightSider"
      ></a-button>
    </a-affix>
    <a-drawer
      title="Basic Drawer"
      placement="right"
      :mask="false"
      @close="onRightSiderClose"
      :visible="rightSiderVisible"
    >
      <a-menu mode="inline" :defaultSelectedKeys="['1']" :selectedKeys="['1']">
        <a-menu-item key="1" @click="showLeftSide">
          <a-icon type="pie-chart" />
          <span>HIS Data</span>
        </a-menu-item>
        <a-menu-item key="2" @click="showLeftSide">
          <a-icon type="desktop" />
          <span>登出</span>
        </a-menu-item>
      </a-menu>
    </a-drawer>
  </div>
</template>
<script>
export default {
  data () {
    return {
      rightSiderVisible: false,
      top: 0
    }
  },
  methods: {
    changeCollapse (key) {
      // console.log(key)
      this.$emit('emitHeaderCollapse', key.indexOf('1') >= 0)
    },
    showLeftSide () {
      this.$emit('emitShowSider')
    },
    onRightSiderClose () {
      this.rightSiderVisible = false
    },
    ShowRightSider () {
      this.rightSiderVisible = !this.rightSiderVisible
    }
  }
}
</script>



