<style scoped>
</style>
<template>
  <div>
    <a-menu mode="inline" :defaultSelectedKeys="['1']" :selectedKeys="['1']">
      <a-menu-item key="1" @click="showLeftSide">
        <a-icon type="pie-chart" />
        <span>Option 1</span>
      </a-menu-item>
      <a-menu-item key="2" @click="showLeftSide">
        <a-icon type="desktop" />
        <span>Option 2</span>
      </a-menu-item>
    </a-menu>
  </div>
</template>
<script>
export default {
  props: {
    showSiderDrawer: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {

    }
  },
  methods: {
    showLeftSide () {
      if (this.showSiderDrawer) {
        this.$emit('emitShowSider')
      }
    }
  }
}
</script>



