<style scoped>
.site-sidebar {
  z-index: 1;
}
.main-menu {
  z-index: 1;
}
.main-menu-inner {
  overflow-x: hidden;
  overflow-y: hidden;
  max-height: 100vh;
}
#nav.ant-menu-horizontal > .ant-menu-submenu:hover {
  border-top: 2px solid #1890ff;
  border-bottom: 2px solid transparent;
}
#nav.ant-menu-horizontal > .ant-menu-item-selected {
  border-top: 2px solid #1890ff;
  border-bottom: 2px solid transparent;
}
#nav.ant-menu-horizontal > .ant-menu-item-selected a {
  color: #1890ff;
}
</style>
<template>
  <a-layout class="min-h-screen">
    <!-- <a-back-top /> -->
    <a-layout-header
      id="layoutHeader"
      style="background: #fff; padding: 0; height: 52px;"
      class="z-40"
    >
      <div
        class="ant-col-xs-0 ant-col-sm-0 ant-col-md-0 ant-col-lg-24 ant-col-xl-24 ant-col-xxl-24"
      >
        <a-affix :offsetTop="top" @change="changeHeaderAffix">
          <Header v-on:emitShowSider="ShowSider" v-on:emitHeaderCollapse="HeaderCollapse"></Header>
        </a-affix>
      </div>
      <div
        class="ant-col-xs-24 ant-col-sm-24 ant-col-md-24 ant-col-lg-0 ant-col-xl-0 ant-col-xxl-0"
      >
        <Header v-on:emitShowSider="ShowSider" v-on:emitHeaderCollapse="HeaderCollapse"></Header>
      </div>
    </a-layout-header>
    <a-layout>
      <a-drawer
        v-if="showSiderDrawer"
        title="Basic Drawer"
        placement="left"
        @close="onSiderClose"
        :visible="siderVisible"
      >
        <Sider v-on:emitShowSider="ShowSider" :showSiderDrawer="showSiderDrawer"></Sider>
      </a-drawer>
      <a-layout-sider
        v-else
        theme="light"
        v-model="reSiderVisible"
        class="ant-col-xs-0 ant-col-sm-0 ant-col-md-0 ant-col-lg-1 ant-col-xl-1 ant-col-xxl-1"
      >
        <a-affix :offsetTop="top + headerHeight" style="width:190px;">
          <section class="main-menu-inner">
            <Sider v-on:emitShowSider="ShowSider" :showSiderDrawer="showSiderDrawer"></Sider>
          </section>
        </a-affix>
      </a-layout-sider>
      <a-layout-content
        class="ant-col-xs-24 ant-col-sm-24 ant-col-md-24 ant-col-lg-23 ant-col-xl-23 ant-col-xxl-23 flex content-between flex-wrap min-h-full"
      >
        <div class="w-screen m-4 mb-6">
          <transition name="fade in">
            <router-view :key="$route.path"></router-view>
          </transition>
        </div>
        <div class="w-screenn m-4 mb-16">Footer</div>
      </a-layout-content>
    </a-layout>
    <a-affix :offsetBottom="bottom">
      <a-menu id="nav" mode="horizontal" v-model="bottomMenu" @click="clickBottomMenu">
        <a-menu-item key="Home">
          <a-icon type="mail" />
          <span>呼吸照護系統</span>
        </a-menu-item>
        <a-menu-item key="Demo">
          <a-badge :count="messengerCnt">
            <a-icon type="appstore" />
          </a-badge>
          <span :class="{'m-2': messengerCnt > 0}">提醒清單</span>
        </a-menu-item>
      </a-menu>
    </a-affix>
  </a-layout>
</template>
<script>
import Header from '@/components/Layout/Header'
import Sider from '@/components/Layout/Sider'
export default {
  data () {
    return {
      showHeaderCollapse: false,
      siderVisible: false,
      scrolled: false,
      headerHeight: 60,
      contentTop: 0,
      messengerCnt: 4,
      top: 0,
      bottom: 0,
      windowHeight: 0,
      windowWidth: 0
    }
  },
  components: {
    Header: Header,
    Sider: Sider
  },
  computed: {
    bottomMenu: {
      get () {
        if (this.$route.name !== 'Demo') {
          return ['Home']
        }
        return ['Demo']
      },
      set () {
        return []
      }
    },
    reSiderVisible () {
      return !this.siderVisible
    },
    showSiderDrawer () {
      // xs: '480px' // sm: '576px' // md: '768px' // lg: '992px' // xl: '1200px' // xxl: '1600px'
      if (this.windowWidth < 992 || this.showHeaderCollapse) {
        return true
      }
      return false
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      window.addEventListener('resize', this.GetWindowHeight)
      window.addEventListener('resize', this.GtWindowWidth)
      window.addEventListener('scroll', this.handleScroll)
      // Init
      this.GetWindowHeight()
      this.GtWindowWidth()
    })
  },
  methods: {
    handleScroll () {
      this.scrolled = window.scrollY > 0
      // if (this.scrolled) {
      //   this.siderVisible = false
      // }
    },
    GetWindowHeight: function (event) {
      this.windowHeight = document.documentElement.clientHeight
      // this.$store.commit({
      //   type: 'Basic/SetWindowWidth',
      //   windowWidth: document.documentElement.clientWidth
      // })
    },
    GtWindowWidth: function (event) {
      this.siderVisible = false
      this.contentTop = 0
      this.windowWidth = document.documentElement.clientWidth
      // this.$store.commit({
      //   type: 'Basic/SetWindowWidth',
      //   windowWidth: document.documentElement.clientWidth
      // })
    },
    HeaderCollapse (pVal) {
      var vuethis = this
      vuethis.showHeaderCollapse = false
      if (pVal && !vuethis.showSiderDrawer) {
        setTimeout(() => {
          vuethis.contentTop = document.querySelector('#patDetail').clientHeight + 40
        }, 500)
      } else {
        vuethis.contentTop = 0
      }
      vuethis.siderVisible = false
      vuethis.showHeaderCollapse = pVal
    },
    changeHeaderAffix (affixed) {
      // console.log(affixed)
    },
    clickBottomMenu (item) {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
      if (this.$route.name !== item.key) {
        this.$router.push({ name: item.key })
      }
    },
    onSiderClose () {
      this.siderVisible = false
    },
    ShowSider () {
      this.siderVisible = !this.siderVisible
    }
  },
  beforeDestroy: function () {
    window.removeEventListener('resize', this.GetWindowHeight)
    window.removeEventListener('resize', this.GtWindowWidth)
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>



