import Home from '@/components/Views/Home'
import Demo from '@/components/Views/Demo'
const webTitle = '振興高壓氧氣治療系統-'

export default [{
  path: '/Main/Home',
  name: 'Home',
  component: Home,
  meta: {
    icon: 'tool',
    titleLink: 'Home',
    title: webTitle + 'Home'
  }
}, {
  path: '/Main/Demo',
  name: 'Demo',
  component: Demo,
  meta: {
    icon: 'tool',
    titleLink: 'Demo',
    title: webTitle + 'Demo'
  }
}]



