<style scoped>
</style>
<template>
  <div>
    <a-card style="width: 300px">
      <a-divider orientation="left">Login Page</a-divider>
      <a-form layout="inline">
        <a-form-item
          required
          label="帳號"
          :validate-status="!!user_id ? '' : 'error'"
          :help="!!user_id ? ' ':'請填寫帳號'"
        >
          <a-input v-model="user_id" />
        </a-form-item>
        <a-form-item
          required
          label="密碼"
          :validate-status="!!User_pwd ? '' : 'error'"
          :help="!!User_pwd ? ' ':'請填寫密碼'"
        >
          <a-input v-model="User_pwd" />
        </a-form-item>
        <a-form-item>
          <a-button type="primary">登入</a-button>
        </a-form-item>
      </a-form>
    </a-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      user_id: '',
      User_pwd: ''
    }
  },
  methods: {

  }
}
</script>



