import Login from '@/components/login'
import Main from '@/components/Layout'
import Mainchildren from '@/router/Mainchildren.js'
const webTitle = '振興高壓氧氣治療系統-'
let _redirect = '/Main/Home'
export default [{
  path: '*',
  redirect: _redirect
},
{
  path: '/Login',
  name: 'Login',
  component: Login,
  meta: {
    title: webTitle + '登入'
  }
},
{
  path: '/Main',
  name: 'Main',
  redirect: '/Main/Home',
  children: Mainchildren,
  component: Main,
  meta: {
    title: '振興高壓氧氣治療系統'
  }
}
]



